import { Component, OnInit, ViewChild } from '@angular/core';
import { AdminService } from '../../servisi/admin.service';
import { PravilaService } from '../../servisi/pravila.service';
import { FormControl, Validators } from '@angular/forms';
import { DatePipe } from '@angular/common';
import { Igra } from '../../modeli/Igra';
import { validateConfig } from '@angular/router/src/config';

@Component
({
  selector: 'app-kreiranje-igara',
  templateUrl: './kreiranje-igara.component.html',
  styleUrls: ['./kreiranje-igara.component.css']
})

export class KreiranjeIgaraComponent implements OnInit
{
  constructor(private adminService: AdminService, private pravilaService: PravilaService)
  {

  }

  // kontrole za unos potrebnih podataka
  private naziv = new FormControl('', [Validators.required]);


  // promenljive za dodavanje bota
  @ViewChild('fajlUpload') fajl;

	// private nazivPravila = new FormControl('', [Validators.required]);
	private uspesno: boolean;
	private neuspesno: boolean;
	private nijeOdabranFajl: boolean;
	private fajlZaUpload: File; // uneti fajl
	private kontrolaZaIgru = new FormControl('',[Validators.required]);

  ngOnInit()
  {
    this.nijeOdabranFajl = false;
		this.fajlZaUpload = null;
		this.uspesno = false;
		this.neuspesno = false;
  }

  // funkcija koja treba da pozove odredjeni servis za dodavanje novo unete IGRE
  DodajIgru(): void
	{
    let igra: Igra = new Igra();

    if (this.fajlZaUpload != null)
    {
      igra.fajlZaPravila = this.fajlZaUpload.name;
    }
    igra.naziv = this.naziv.value;

    console.log(igra);

		if (this.fajlZaUpload != null)
    {
        // prosledjujemo servisu objekat Igra i fajl koji smo ucitali
        this.pravilaService.DodajIgru(igra, this.fajlZaUpload).subscribe(odgovor =>
        {
            if (odgovor === 1)
            {
                // neki FENSI modal za potvrdu da je dodat
                console.log(odgovor);
                this.uspesno = true;
            }
            else
            {
                // modal za gresku
                console.log(odgovor);
                this.neuspesno = true;
            }
        });
    }
    else
    {
        this.nijeOdabranFajl = true;
    }
	}

  PostaviFajl(event): void
	{
		console.log(event);
		let files = event.target.files;

		if (files.length > 0)
		{
			this.fajlZaUpload = files[0];
		}
	}
}



