import { Injectable } from '@angular/core';
import { Igra } from '../modeli/Igra';
import { Observable } from 'rxjs/Observable';
import { environment } from '../../environments/environment';
import { HttpClient, HttpHeaders } from '@angular/common/http';

@Injectable()
export class PravilaService {

	constructor(
		private http: HttpClient
	) { }
	
	// primamo objekat tipa Igra sa nazivom igre i nazivom fajla
	// kao i fajl koji smo ucitali
	DodajIgru(igra: Igra, fajlZaUpload: File): Observable<any>
	{
		// url servera
		let url = environment.serverUrl + 'dodajIgru';

		// moramo biti ulogovani
        const bearerHeader: string = 'Bearer ' + localStorage.getItem('token');
		let headers = new HttpHeaders().set('authorization',bearerHeader);
		
		// pravimo formu
		let formData = new FormData();
		console.log(fajlZaUpload.name);
		formData.append('file', fajlZaUpload, fajlZaUpload.name); // ucitan fajl
		formData.append('fileName', fajlZaUpload.name); // saljemo posebno ime fajla posto nije htelo da radi
		formData.append('username',localStorage.getItem('username')); // ko je ulogovan
		formData.append('naziv', igra.naziv); // naziv igre bi ovde trebalo da saljemo

        //headers.append('authorization', bearerHeader);
		//headers.append('authorization', bearerHeader);
		/*
		let zahtev =  
		{
			username: localStorage.getItem('username'), 
			bot: bot, 
			fajl: fajl 
		};
		*/

        //console.log(zahtev.fajl);

		// saljemo rikvest!
		return this.http.post(url, formData, { headers: headers });
  	}

	// vraca objekat:
	// idBota,naziv (igre),nazivFajla,idKorisnika
	
	/*
	UzmiBotoveKorisnika(): Observable<any>
	{
		let url = environment.serverUrl + localStorage.getItem('username')+"/botovi";

		const bearerHeader: string = 'Bearer ' + localStorage.getItem('token');
		let headers = new HttpHeaders().set('authorization',bearerHeader);

		return this.http.get(url, {headers : headers });
	}
	*/
}



